.text-white {
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
}

.underline {
  text-decoration-line: underline;
}

.outline-none {
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.transition-opacity {
  transition-property: opacity;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.\!duration-100 {
  transition-duration: 100ms !important;
}

:root {
  --color-1: #121212;
  --color-2: #8f8f8f;
  --color-3: #2d2d2d;
  --color-4: #dde6ed;
  --color-5: rgb(220, 38, 38);
}

body {
  background: var(--color-1);
  height: -webkit-max-content !important;
  height: max-content !important;
}

.bg-white-scroll {
  position: fixed;
  background-color: #000;
  z-index: 9999;
}

.hover\:bg-\[--color-5\]:hover {
  background-color: var(--color-5);
}

.hover\:font-bold:hover {
  font-weight: 700;
}

.hover\:text-\[--color-5\]:hover {
  color: var(--color-5);
}

.focus\:outline-none:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}

@media (min-width: 768px) {
  .md\:col-span-2 {
    grid-column: span 2 / span 2;
  }

  .md\:col-span-3 {
    grid-column: span 3 / span 3;
  }

  .md\:col-span-4 {
    grid-column: span 4 / span 4;
  }

  .md\:col-span-9 {
    grid-column: span 9 / span 9;
  }

  .md\:mb-0 {
    margin-bottom: 0px;
  }

  .md\:mt-0 {
    margin-top: 0px;
  }

  .md\:block {
    display: block;
  }

  .md\:w-\[25\%\] {
    width: 25%;
  }

  .md\:w-\[50\%\] {
    width: 50%;
  }

  .md\:w-\[75\%\] {
    width: 75%;
  }

  .md\:flex-row {
    flex-direction: row;
  }

  .md\:gap-4 {
    gap: 1rem;
  }

  .md\:text-left {
    text-align: left;
  }
}

@media (min-width: 1024px) {
  .lg\:col-span-3 {
    grid-column: span 3 / span 3;
  }

  .lg\:w-\[30\%\] {
    width: 30%;
  }

  .lg\:flex-row {
    flex-direction: row;
  }
}
  